/*----------------------------------*\
		Form
\*----------------------------------*/
/* Form row properties */
.form-row {
	margin-bottom: 1rem;
	padding-right: 1rem;
	padding-left: 1rem;
	label {
		font-size: 0.9rem;
	}
}
/* Form control properties - child of form row */
.form-control {
	font-family: inherit;
	font-size: inherit;
	line-height: 36px;
	display: block;
	width: 100%;
	height: 36px;
	padding: 0 12px;
	border: 1px solid #f8f5f5;
	border-radius: 0;
	outline: none;
	background-color: $color-white;
	background-image: none;
	@include transition(.2s ease-in-out);
	&:focus {
		border-color: #979797;
	}
	&--large {
		font-size: 20px;
		height: 54px;
		padding: 0 24px;
	}
	&--newsletter {
		@media #{$small-and-up} {
			flex: 1;
		}
		@media #{$extra-small-and-down} {
			width: 100%;
			margin-bottom: 15px;
		}
	}
}
/* Textarea properties */
textarea {
	max-width: 100%;
	min-height: 150px;
}
/* Checkbox properties */
input[type=checkbox] {
	outline: none;
}
