/*----------------------------------*\
    Buttons
\*----------------------------------*/
.btn {
    display: inline-block;
    overflow: hidden;
    padding: 15px 30px;
    text-align: center;
    vertical-align: middle;
    white-space: nowrap;
    text-decoration: none;
    text-overflow: ellipsis;
    border: none;
    border-radius: 8px;
    font-size: 28px;
    font-weight: bold;
    @include box-shadow(0px 0px 5px 0px rgb(255, 255, 255));
    @include transition(.2s ease-in-out);
    /* Hover state */
    &:hover {
        color: $color-white;
        box-shadow: 0 1px 1.5px 0 rgba(0, 0, 0, .24);
    }
    /* Focus state shows only on mobile */
    @media #{$small-and-down} {
        &:focus {}
    }
    &--border{
        border: solid 4px $color-white;
    }
    /* Bordered button with transparent background */
    /* Floating button - usually with icon inside */
    &--round {
        border-radius: 50%;
    }
    /* Large button - same style as default one, just bigger */
    &--large {
        font-size: 1.2rem;
        padding: 18px 24px;
    }
    /* background colors */
    &--bg-transparent {
        background: none;
        &:hover{
            background: rgba(255,255,255,0.3);
        }
    }
    &--bg-blue{
        background-color: $color-blue;
    }
    /* text colors */
    &--text-white {
        color: $color-white;
    }
    span {
        vertical-align: middle;
    }
}
