/*----------------------------------*\
		Headings
\*----------------------------------*/
h1 {
    font-size: 72px;
    text-shadow: 0px 2px 2px rgba(0,0,0,0.4),
    	           0px 8px 13px rgba(0,0,0,0.1),
      	         0px 18px 23px rgba(0,0,0,0.1);
    @media #{$extra-small-and-down} {
        font-size: 40px;
    }
}
h2 {
    font-size: 40px;
    line-height: 40px;
    text-shadow: 0px 2px 2px rgba(0,0,0,0.4),
    	           0px 8px 13px rgba(0,0,0,0.1),
      	         0px 18px 23px rgba(0,0,0,0.1);
    @media #{$extra-small-and-down} {
        font-size: 24px;
    }
}
