/*----------------------------------*\
    Images
\*----------------------------------*/
.img {
    display: block;
    width: 100%;
    height: auto;
    /* round image */
    &--circle {
        border-radius: 50%;
    }
    /* image hover overlay */
    &-hover {
        position: relative;
        display: block;
        width: 100%;
        height: auto;
        /* color of overlay */
        &--black {
            &:before {
                background-color: rgba(0, 0, 0, 0);
            }
            &:hover:before {
                background-color: rgba(0, 0, 0, .7);
            }
        }
        /* overlay properties */
        &:before {
            position: absolute;
            z-index: 1;
            top: 0;
            left: 0;
            display: block;
            width: 100%;
            height: 100%;
            content: '';
            @include transition(.2s ease-out);
        }
        /* content visible on hover */
        &:hover {
            &:after {
                visibility: visible;
                opacity: 1;
            }
        }
        /* overlay content properties */
        &:after {
            position: absolute;
            z-index: 2;
            top: 0;
            left: 0;
            display: flex;
            visibility: hidden;
            width: 100%;
            height: 100%;
            opacity: 0;
            justify-content: center;
            align-items: center;
            @include transition(.2s ease-out);
        }
        &--content-sth:after {
            content: 'blabla';
        }
    }
}
