    /* overlay content properties */ }
    .img-hover--black:before {
      background-color: transparent; }
    .img-hover--black:hover:before {
      background-color: rgba(0, 0, 0, 0.7); }
    .img-hover:before {
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      display: block;
      width: 100%;
      height: 100%;
      content: '';
      -webkit-transition: 0.2s ease-out;
      -moz-transition: 0.2s ease-out;
      -ms-transition: 0.2s ease-out;
      -o-transition: 0.2s ease-out;
      transition: 0.2s ease-out; }
    .img-hover:hover:after {
      visibility: visible;
      opacity: 1; }
    .img-hover:after {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      display: flex;
      visibility: hidden;
      width: 100%;
      height: 100%;
      opacity: 0;
      justify-content: center;
      align-items: center;
      -webkit-transition: 0.2s ease-out;
      -moz-transition: 0.2s ease-out;
